import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { getDefaultReportBasicValues, ReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';

const CLASS_KEY: string = 'ajusteimportacaomanadrelrv';
const REPORT_EVENT: string = 'printAjusteImportacaoManad';
const REPORT_TITLE: string = 'Ajustes na importação MANAD';

const DEFAULT_VALUES: ReportBasicValues = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
};

const AjusteImportacaoManadReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<ReportBasicValues>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Ajustes na importação MANAD"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        extraConfigOpened={true}
      />
    </WindowComponent>
  );
};

export default AjusteImportacaoManadReportView;
